
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  /*margin-top: 60px;*/
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.drag-header {
  border-bottom: 1px solid rgba(0, 0, 0, .3);
}

.drag-box {
  position: relative;
  width: 100%;
  min-height: 500px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 10px;
  background: rgba(0, 0, 0, 0.1);
}
.x-window {
  position: absolute;
  width: 300px;
  height: 200px;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background: #fff;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  overflow: hidden;
}
.x-window .x-window-header {
  height: 30px;
  line-height: 30px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
  background: #f5f7f9;
  cursor: default;
}
.x-window .x-window-header:hover {
  background: rgba(0, 0, 0, 0.1);
}
.x-window .x-window-header .x-window-title {
  padding: 0 10px;
  text-align: left;
  cursor: default;
}
.x-window .x-window-body {
  padding: 10px;
}
.x-window .x-window-body .x-window-bar {
  padding: 10px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: #f5f7f9;
  cursor: default;
}
.x-window .x-window-body .x-window-bar:hover {
  background: rgba(0, 0, 0, 0.1);
}
.x-drag-start,
.x-drag-move {
  -webkit-transition: none;
  transition: none;
  opacity: .7;
}
.x-drag-start .x-window-header .x-window-title,
.x-drag-move .x-window-header .x-window-title {
  cursor: move !important;
}
.x-drag-start .x-window-body .x-window-bar,
.x-drag-move .x-window-body .x-window-bar {
  cursor: move !important;
}
.app-window-resize {
  width: 20px;
  height: 20px;
  position: absolute;
  background: transparent;
}
.app-window-resize.resize-top-left {
  cursor: nw-resize;
  top: 0;
  left: 0;
}
.app-window-resize.resize-top-right {
  cursor: ne-resize;
  top: 0;
  right: 0;
}
.app-window-resize.resize-bottom-left {
  cursor: sw-resize;
  bottom: 0;
  left: 0;
}
.app-window-resize.resize-bottom-right {
  cursor: se-resize;
  bottom: 0;
  right: 0;
}
.app-window-resize.resize-top-border {
  cursor: ns-resize;
  top: 0;
  width: 100%;
  height: 2px;
}
.app-window-resize.resize-right-border {
  cursor: ew-resize;
  right: 0;
  width: 2px;
  height: 100%;
}
.app-window-resize.resize-bottom-border {
  cursor: ns-resize;
  bottom: 0;
  width: 100%;
  height: 2px;
}
.app-window-resize.resize-left-border {
  cursor: ew-resize;
  left: 0;
  width: 2px;
  height: 100%;
}
